@import url(https://fonts.googleapis.com/css?family=Big+Shoulders+Text&display=swap);
body {
  margin: 0;
  font-family: 'Big Shoulders Text', sans-serif;
}

h1, h2, h3, h4, h5, p, a {
  font-family: 'Montserrat', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.route-wrapper {
  position: absolute;
}

.route-wrapper > div {
  position: absolute;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.wrapperHome {
  margin: 0;
  width: 100%;
  height: 100vh;
}

.wrapperHome .row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  padding: 0;
  margin: 0;
}

.wrapperHome .column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1 1;
  background: #000000;

}

.wrapperHome .column.contenido {
  flex: 12 1;
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

.wrapperHome .blue-column {
  height: 100vh;
  display: flex;
  align-items: center;
  background: #111;
  justify-content: center;
  box-shadow: 0 0 10px inset #000000;
}

.wrapperHome .blue-column i.material-icons {
  font-size: 2em;
  color: #ffffff;
  margin: 20px 0;
  font-size: 1.2em;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.wrapperHome .blue-column i.material-icons:hover {
  color: #ff0000;
}

.wrapperHome .green-column {
  height: 100vh;
  display: flex;
  align-items: center;
  text-align: left;
  padding: 20px;
  color: #333;
}

.wrapperHome .green-column a {
  color: #000000;
}



.datos ul li {
    font-size: 0.5em;
}
.entrada {
    margin: 0;
    padding: 0;
    text-align: left;
}

.entrada i {
    background: #ffffff;
    color: #000000;
    padding: 5px;
    border-radius: 50%;;
}

p {
    font-size: 0.8em;
    text-align: justify;
}

h4.titulo {
    text-align: left;
    font-weight: 100;
    color: #333333;
    font-size: 1.2rem;
    margin-bottom: 10px;
    width: 100%;;
}

h4.titulo i {
    background: #333333;
    padding: 10px;
    color: #ffffff;
    margin: 5px;
}
.d-flex {
    margin: 0;
    padding: 0;
}
.container {
    align-Items: center;
    margin: 0;
    padding: 0;
    width: 100%;
}
.column.contenido a:hover  {
    text-decoration: none;
}
.contenido {
    width: '100%';
    height: '90vh';
    margin: '5px';
    display: 'flex';
    align-items: 'center';
    flex-direction: 'column';
    justify-content: 'center';
    border-top: '5px solid #111';
    overflow: 'hidden'
}

.column.contenido {
    background: #f2f2f2;
    position: absolut;
}

.column.contenido h1 {
    color: #000;
    font-size: 2em;
    font-family: 'Montserrat', sans-serif;
}

.column.contenido h2 {
    color: #222;
    font-family: 'Montserrat', sans-serif;
}

.column.contenido p {
    color: #444;
    font-family: 'Montserrat', sans-serif;
}

.lni-angle-double-right {
    cursor: pointer;
    -webkit-transition: all 1s;
    transition: all 1s;
}

.lni-angle-double-right:hover {
    color: red;
}

.picture {
    background: #000000;
    height: 100px;
    width: 100px;
    margin: 5px;
    border-radius: 50%;
    border: 5px solid #fff;
    overflow: hidden;
    margin-bottom: 20px;
}
.technical ul {
    list-style: none;
    color: #000000;
}
.technical ul li {
    text-align: left;
}
.technical ul li ul li, .technical ul li ul li p {
    margin: 0;
    padding: 0;
}

p.redes {
    color: #fff !important;
    font-size: 0.8rem;
    padding: 10px 0;
    margin: 0;
    font-weight: 100;
    padding-left: 20px;
    border-bottom: 1px solid #444;
}

.colProfile h1 {
    font-size: 1em !important;
    font-weight: bold;
    text-align: left;
    padding-left: 10px;
    border-bottom: 1px solid #fff;
    padding-bottom: 10px;
}
.colProfile h1 i.lni {
    position: relative;
    float: right;
    right: 0px;
    padding-top: 3px;
    -webkit-transition: all 0.5s linear;
    transition: all 0.5s linear;
    cursor: pointer;
}
.colProfile h1 i.lni:hover {
    background: red;
    color: #fff;
}
.colProfile h1 i.lni.flecha.seleccionado {
    background: red;
    color: #fff !important;
}
.colProfile p {
    font-size: 0.5em !important;
    font-weight: 100;
    text-align: justify;
    padding-left: 30px;
    padding-bottom: 30px;
    padding-top: 30px;
}
.flecha {
    padding: 0;
    margin: 0;
    padding: 7px !important;
    margin-top: -5px;
    border: 1px solid #fff;
    margin-left: 10px;
}
.column.contenido h1.seleccionadoText {
    color: red !important;
}
